@import url(/src/Variables.css);
@import url(/src/Variables.css);
@import url(/src/Variables.css);
@import url(/src/Variables.css);
@import url(/src/Variables.css);
:root {
  --brand-blue-regal: #213d55;
  --brand-blue-tranquil: #dae8eb;
  --brand-blue-spindle: #c0d6dd;
  --brand-blue-pelorous: #2d9ec5;
  --brand-orange-bittersweet: #ff6655;
  --greyscale-grey: #666666;
  --greyscale-grey-lightSlate: #7e93a0;
  --greyscale-grey-nero: #232323;
  --secondary-error: #ff2929;
  --secondary-blue-turquoise: #20b2aa;
  --secondary-blue-darkPalette: #112A40;
}

/* GENERAL RESET */
html, body {
  background-color: var(--brand-blue-regal);
}
body {
  background-color: var(--brand-blue-regal);
  color: #fff;
  line-height: 1.5;
  font-size: 16px;
  font-family: 'Roboto','Helvetica Neue', sans-serif;
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-y: scroll;
  overflow-x: hidden;
  scroll-behavior: smooth;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

a {
  display: block;
}

ul, ol, li {
  margin: 0;
  padding: 0;
}


/* FORM RESET */
form {
  overflow: auto;
}
fieldset {
  border: none;
  margin: 0;
  padding: 1em 0;
}


/* BODY */
#root {
  position: relative;
}

.Logo {
  max-width: 125px;
}



input[type="search"] {
  border: none;
  border-radius: 20px;
  font-size: 16px;
  height: 43.5px;
  padding: 15px;
  width: 100%;
}

.Header {
  padding: 0.5em 0.5em 1em;
}
.Header-list {
  align-items: center;
  display: flex;
  flex-flow: wrap;
  justify-content: space-between;
}
.Header-list__item--search {
  width: 100%;
}
.Header-list__item--subscribe {
  margin: 0 auto;
}
.subscribe-btn {
  background-color: var(--brand-orange-bittersweet);
  border-radius: 25px;
  border: none;
  color: #fff;
  font-size: 16px;
  font-weight: normal;
  height: 43.5px;
  padding: 5px 15px;
  width: 160px;
}

/* RESPONSIVE */
@media screen and (min-width: 720px) {
  .Header {
    background: var(--secondary-blue-darkPalette);
    padding: 1em 5% 5px;
  }
  .Header-list__item--search {
    width: 70%;
  }
  .Header-list__item--subscribe {
    margin: 0;
  }
}

@media screen and (min-width: 992px) {}
@media screen and (min-width: 1024px) {
  .Header {
    padding: 1em 10% 5px;
  }
}
@media screen and (min-width: 1280px) {}
@media screen and (min-width: 1366px) {}
@media screen and (min-width: 1440px) {}
@media screen and (min-width: 1600px) {}
@media screen and (min-width: 1920px) {}

/* HEADER WRAPPER */
.StickyWrapper {
  background-color: var(--brand-blue-regal);
  position: relative;
  height: auto;
  z-index: 100;
}
.Sticky {
  -webkit-transition: all 2s ease;
  transition: all 2s ease;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
}

/* NAVIGATION */
.Navigation {
  align-items: center;
  background: var(--secondary-blue-darkPalette);
  display: flex;
  position: relative;
  margin: 0 auto;
  justify-content: center;
  text-align: center;
}

.Nav-list {
  margin: 0;
  padding: 0;
}
.Nav-list__item {
  display: inline-block;
  padding: 5px 8px;
  margin: 0 5px;
}
.Nav-list__link {
  color: var(--brand-blue-pelorous);
  display: block;
  font-size: 18px;
  text-decoration: none;
}
.Nav-list__link:hover,
.Nav-list__link:focus {
  text-decoration: underline;
}

.Nav-list__link--subtext {
  font-weight: normal;
  font-size: 15px;
  margin: 0 2px;
}


@media screen and (min-width: 720px) {
  .Navigation {
    padding: 5px 5%;
    box-shadow: 0px 10px 5px -8px rgba(4,24,40,1);
  }
}

@media screen and (min-width: 1024px) {
  .Navigation {
    padding: 5px 10% 1em;
  }
}

.Banner {
  position: relative;
}
.Banner--inner {
  color: var(--brand-orange-bittersweet);
  padding: 1em 0.5em;
  text-align: center;
}
.Banner__title {
  font-size: 22px;
  padding: 0 0.5em;
}
.Banner__author {
  margin: 0;
}

@media screen and (min-width: 720px) {
  .Banner--inner {
    padding: 1em 5%;
  }
  .Banner__title {
    padding: 0 15%;
  }
}


@media screen and (min-width: 1024px) {
  .Banner--inner {
    padding: 1em 10%;
  }
  .Banner__title {
    padding: 0 20%;
  }
}

.Articles {
  font-size: 14px;
  padding: 1em 0.5em;
  position: relative;
}

.Articles-info {
  color: #fff;
}
.Articles-info-wrapper {
  overflow: auto;
}
.Articles-info__title {
  color: var(--brand-blue-pelorous);
  font-size: 18px;
  padding: 0 0 5px;
  line-height: 1;
  text-transform: uppercase;
}
.Articles-info__heading {
  color: var(--brand-blue-tranquil);
  font-size: 20px;
}
.Articles-info__list {
  list-style-position: inside;
}
.Articles-info__list-item {
  list-style-type: circle;
  margin: 0;
}

.Articles__list {
  overflow: auto;
}
.Articles__item {
  margin: 10px;
  overflow: hidden;
}
.Articles__image {
  height: 120px;
  overflow: hidden;
  border-radius: 5px;
}
.Articles__title {
  font-size: 16px;
}
.Articles__text p {
  margin: 0;
}
.Articles-media__source iframe {
  background-color: var(--greyscale-grey-lightSlate);
  width: 100%;
}


@media screen and (min-width: 720px) {
  .Articles {
    align-items: stretch;
    display: flex;
    padding: 2em 5%;
    justify-content: space-between;
  }

  /* COLUMN ONE */
  .Articles-media {
    width: 55%;
    height: 420px;
  }

  .Articles-media__source iframe {
    height: 420px;
  }

  /* COLUMN TWO */
  .Articles-info {
    width: 45%;
    height: 420px;
    position: relative;
    overflow-y: scroll;
    overflow-x: hidden;
  }
  .Articles-info::-webkit-scrollbar {
    width: 5px;
  }
  .Articles-info::-webkit-scrollbar-track {
    border-radius: 10px;
    -webkit-box-shadow: inset 0 0 5px rgb(19,35,48);
  }
  .Articles-info::-webkit-scrollbar-thumb {
    background-color: var(--brand-orange-bittersweet);
    border-radius: 10px;
    outline: 1px solid var(--brand-orange-bittersweet);
  }
  .Articles-info-wrapper {
    padding: 0 1em;
  }
  .Articles-info__list {
    padding: 5px 0;
  }

  .Articles__item {
    display: flex;
  }
  .Articles__image {
    border-radius: 2px;
    margin: 0 10px 0 0;
    height: 110px;
    overflow: hidden;
    width: 25%;
  }
  .Articles__excerpt {
    width: 55%;
  }
}

@media screen and (min-width: 1024px) {
  .Articles {
    padding: 2em 10%;
  }
}

.Footer {
  background-color: #fff;
  padding: 1em 0.5em;
  margin: 2em 0 0;
}
.Footer--inner {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
}


.Menu {
  overflow: auto;
}
.Menu-list {}
.Menu-list__item {
  display: inline-block;
  margin: 0 1em;
}
.Menu-list__anchor {
  color: var(--brand-blue-pelorous);
  font-weight: 700;
  text-decoration: none;
  font-size: 20px;
}

.Logo__image {
  max-width: 125px;
}

@media screen and (min-width: 720px) {
  .Footer {
    padding: 3em 5%;
  }
  .Footer--inner {
    padding: 10px 0 0;
  }
}

@media screen and (min-width: 1024px) {
  .Footer {
    padding: 3em 15%;
  }
}

.Media-banner {
  width: 100%;
}
.Media-banner--inner {
  height: 35vh;
  overflow: hidden;
}
.Media-banner__video {
  background-color: var(--greyscale-grey-lightSlate);
  width: 100%;
}

